    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

.item {
  list-style-type: none;
}

.link {
  text-decoration: none;
}

.menu, .site-nav {
  align-items: center;
  display: flex;
}

.site-nav__link {
  color: inherit;
}

.logo {
  font-family: var(--basic-font);
  letter-spacing: .02em;
  text-align: center;
  color: var(--accent-dark-orange);
  font-size: 28px;
  font-weight: 400;
  line-height: .857143;
  position: relative;
}

.logo__svg:after {
  content: url("cocktail.0854a1f4.svg");
  z-index: -1;
  fill: #000;
  stroke: #000;
  display: block;
  position: absolute;
  top: -20px;
  right: 15px;
}

.search-form {
  display: flex;
}

.mobile-menu__item {
  margin-top: 35px;
}

.search-form__input {
  color: inherit;
  background-color: inherit;
  width: 280px;
  height: 40px;
  border: 1px solid var(--accent-dark-orange);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  padding: 6px 8px 6px 85px;
}

.search-form__input::-ms-input-placeholder {
  letter-spacing: .02em;
  color: #5f6775;
  opacity: .4;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.search-form__input::placeholder {
  letter-spacing: .02em;
  color: #5f6775;
  opacity: .4;
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.search-form {
  position: relative;
}

.search-form:before {
  content: "Search";
  color: #5f6775;
  opacity: .4;
  border-right: 2px solid #5f6775;
  padding-right: 5px;
  display: block;
  position: absolute;
  top: 11px;
  left: 45px;
}

.search-form__btn {
  height: 40px;
  border: 1px solid var(--accent-dark-orange);
  background-color: inherit;
  border-right: 0;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.mobile-menu {
  z-index: 1;
  width: 100%;
  height: 100%;
  background: var(--background-light);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  padding: 0 20px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  overflow-y: auto;
  transform: translate(-100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translate(0);
}

.mobile-menu__btn {
  background-color: var(--background-light);
  border: 0;
  padding: 0;
  line-height: 0;
  display: block;
}

.mobile-menu__close-btn {
  width: 32px;
  height: 32px;
  border: 0;
}

.mobile-menu__close-icon {
  fill: var(--basic-font);
  margin-top: 22px;
  margin-right: 20px;
}

.mobile-menu__btn {
  background-color: var(--background-light);
  border: 0;
  margin-top: 30px;
  padding: 0;
  line-height: 0;
  display: block;
}

.mobile-menu__close-btn {
  z-index: 2;
  background: none;
  border: 0;
  padding: 0;
  line-height: 0;
  display: block;
  position: absolute;
  top: 4px;
  right: 4px;
}

.site-nav__switcher {
  display: none;
}

